	module.exports = {"data":{"markdownRemark":{"html":"<h2>Amazon Web Services</h2>\n<p>Amazon Web Services, or AWS for short, is a subsidiary of Amazon.com that provides on-demand cloud computing platforms to individuals, companies and governments on a\npaid subscription basis with a free-tier option available for 12 months. It along with Microsoft’s Azure and Google’s Cloud Platform are considered the three major\npublic cloud vendors.</p>\n<p>The technology allows subscribers to have at their disposal a full-fledged virtual cluster of computers, available all the time, through the internet. AWS’s version of\nvirtual computers have most of the attributes of a real computer including hardware (CPU(s) &#x26; GPU(s) for processing, local/RAM memory, hard-disk/SSD storage); a choice\nof operating systems; networking; and pre-loaded application software such as web servers, databases, CRM, etc. Each AWS system also virtualizes its console I/O (keyboard,\ndisplay, and mouse), allowing AWS subscribers to connect to their AWS system from anywhere in the world using a modern browser. The browser acts as a window into the\nvirtual computer, letting subscribers log-in, configure and use their virtual systems just as they would a real physical computer.</p>\n<p>Subscribers can choose to deploy their AWS systems to provide internet-based services for their own and their customers’ benefit.  Since individuals or organizations\nusing AWS do not physically own the AWS infrastructure, they do not have to deal with some of the maintenance tasks associated with owning a physical datacenter such as\noperating networking equipment.</p>\n<p>AWS in particular has also been expanding on their cloud offerings. They are providing innovative services such as AWS Lambda, in which users only need to focus on\nwriting code and not on the servers that will be running their code (giving rise to the new <em>serverless</em> movement). As a result of these benefits, cloud computing\nplatforms such as AWS have been popular among startups who might initially have less capital to invest into their Information Technology or IT. Instead, they can focus\non building great products for their users and growing their business over time with AWS’s on demand model.</p>\n<p>Popular AWS services include:</p>\n<ul>\n<li>Virtual Machines via the EC2 (Elastic Compute Cloud) platform</li>\n<li>Databases including both SQL and NoSQL</li>\n<li>S3 (Simple Storage Service)</li>\n<li>SQS (Simple Queue Service)</li>\n<li>Games development via the Amazon Lumberyard game development engine and subsequent hosting platform.</li>\n<li>DynamoDB (Amazon Dynamo Data Base)</li>\n<li>CloudWatch (Metrics and Alarms)</li>\n<li>CodeDeploy (Deployments)  </li>\n<li>Lambda (Serverless)</li>\n<li>Application Monitoring tools</li>\n<li>Mobile development tools and platforms</li>\n<li>Amazon Alexa development area</li>\n</ul>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href=\"https://aws.amazon.com/\">AWS</a></li>\n</ul>","fields":{"slug":"/cloud-development/amazon-aws/"},"frontmatter":{"title":"Amazon Web Services"}}},"pathContext":{"slug":"/cloud-development/amazon-aws/"}}

